.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  margin: 0 auto;
  min-width: 320px;
  max-width: 500px;
  box-sizing: border-box;
  padding-right: 20px;
  padding-left: 20px; }

body {
  margin: 0; }

.btn {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center; }

.btn--green {
  color: #fff;
  background-color: #68b738; }

.btn--transparent {
  color: #000;
  background-color: transparent; }

