@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff"); }

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.header__logo {
  height: 25px;
  width: 110px; }

.header-navigation {
  margin: 1px; }

.main-navigation {
  background-color: #fff;
  width: 100%; }

.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 20px;
  width: 100%; }

.site-list__item {
  margin: 1px;
  text-align: center;
  box-sizing: border-box;
  padding: 25px 10px;
  border-top: 1px solid #e6e6e6; }

.site-list__item:last-child {
  border-bottom: 1px solid #e6e6e6; }

.site-list__link {
  padding: 25px 10px;
  color: #000;
  text-decoration: none; }

.intro {
  color: #fff;
  font-family: "Oswald", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 22px;
  background: url("../img/intro-mobile.png"), linear-gradient(to bottom, #64a83b 0%, #64a83b 61.5%, #fff 61.5%);
  background-repeat: no-repeat;
  background-position: center top; }

.intro__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.intro__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  margin-top: 28px; }

.intro__desc {
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 14px;
  margin-top: 22px; }

.intro__link {
  margin-top: 302px;
  min-width: 280px;
  height: 40px; }

.category {
  display: block; }

.category__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.category__item {
  background-color: #f2f2f2;
  margin-bottom: 20px;
  padding: 30px 21px 9px 20px; }

.category__wrapper {
  border-bottom: 1px solid #e6e6e6; }

.category__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 37px;
  text-align: center;
  padding-bottom: 29px; }

.category__slim {
  margin-left: 24px; }

.category__set {
  margin-left: 51px; }

.category__desc {
  font-family: "Arial",sans-serif;
  font-size: 14px;
  line-height: 18px;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  padding-bottom: 22px; }

.category__link {
  font-size: 16px;
  padding: 10px 10px 10px 0;
  margin-top: 10px;
  justify-content: left; }

.advantages__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-size: 36px; }

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding: 0 20px 1px 20px;
  margin-top: 35px;
  box-sizing: border-box; }

.advantages__item {
  padding-left: 60px;
  margin-bottom: 26px; }

.advantages__desc {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px; }

.presentation {
  background-color: #f2f2f2;
  box-sizing: border-box;
  padding-top: 35px;
  width: 100%;
  box-sizing: border-box; }

.presentation__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-size: 36px;
  line-height: 60px;
  margin-left: 18px; }

.presentation__desc {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin-left: 18px;
  margin-top: 23px; }

.presentation__result {
  margin-left: 20px; }

.presentation__cost {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  margin: 15px 0 0 0; }

.presentation__slider {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.result {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  box-sizing: border-box;
  padding-top: 26px; }

.result__wrapper {
  width: 124px;
  text-align: center; }

.result__time,
.result__weight {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0px; }

.result__desc {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 12px;
  line-height: 12px;
  display: inline-block;
  width: 60px;
  margin: 10px 0 0 0; }

.slider {
  height: 362px; }

.slider__slide {
  min-height: 289px; }

.slider__slide--before {
  padding: 1px; }

.slider__slide--after {
  padding: 1px; }

.slider__controls {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.slider__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase; }

.contacts__desc--main {
  padding-top: 27px;
  padding-bottom: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.contacts__desc--catalog {
  padding-top: 27px;
  padding-bottom: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #f2f2f2; }

.contacts__invitation {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  width: 140px; }

.contacts__wrapper {
  width: 140px; }

.contacts__adress,
.contacts__city {
  font-family: "Arial", "Oswald", sans-serif;
  font-size: 14px;
  line-height: 20px;
  margin: 0; }

.contacts__map {
  width: 100%; }
  .contacts__map img {
    width: 100%;
    height: auto;
    object-fit: contain; }

.footer {
  background-color: #f2f2f2;
  padding-bottom: 35px;
  padding-top: 40px; }

.footer__wrapper {
  padding: 1px; }

.footer__social {
  margin: 1px; }

.footer__academy {
  margin: 1px; }

.footer__link {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  text-decoration: none; }

.footer__mail {
  position: relative; }

.logo {
  background-color: #000; }

.logo--header {
  width: 100px; }

.logo--footer {
  width: 150px; }

.social {
  padding: 1px;
  list-style: none;
  margin: 0;
  padding: 0; }

.social__item {
  width: 50px;
  height: 50px; }

.social__link {
  background-color: red; }

.social__link--vk {
  background-color: red; }

.social__link--inst {
  background-color: red; }

.social__link--fb {
  background-color: red; }

/*страница каталог*/
.catalog__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-size: 36px;
  font-weight: normal;
  line-height: 60px; }

.catalog__more-product {
  margin: 0px; }

.cards {
  list-style: none;
  margin: 0;
  padding: 0; }

.cards__item {
  box-sizing: border-box;
  border-top: 1px solid #e6e6e6; }

.cards__item:last-child {
  border-bottom: 1px solid #e6e6e6; }

.card {
  padding: 26px 0 25px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.card__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between; }

.card__img {
  width: 143px;
  height: 143px; }

.card__wrapper-table {
  width: 100%; }

.card__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-size: 16px;
  font-weight: normal;
  line-height: 20px;
  text-transform: uppercase;
  display: block;
  width: 75px; }

.card__table {
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 19px;
  font-size: 12px;
  line-height: 16px;
  width: 100%; }

.card__rigth {
  text-align: right; }

.card__link {
  margin: 24px;
  min-width: 240px;
  height: 40px; }

.warehouse__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: normal;
  line-height: 20px;
  text-align: center; }

.warehouse-desc {
  font-size: 12px;
  line-height: 16px;
  text-align: center; }

.warehouse__link {
  text-align: center;
  color: #000; }

.extra {
  padding-top: 33px;
  box-sizing: border-box; }

.extra__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-weight: normal; }

.extra__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.extra__item {
  box-sizing: border-box;
  padding-top: 15px;
  padding-bottom: 17px; }

.extra__gift {
  margin-top: 39px; }

.additional-product {
  border-top: 1px solid #f2f2f2; }

.additional-product:last-child {
  border-bottom: 1px solid #f2f2f2; }

.additional-product__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  margin: 0;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px; }

.additional-product__price {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 14px;
  line-height: 14px; }

.additional-product__link {
  min-width: 240px;
  height: 40px; }

.prize {
  background-color: #68b738; }

.prize__desc {
  font-size: 16px;
  line-height: 20px;
  color: #fff; }
